import React, {Component} from 'react';

import Timer from "./timer";
import datadefault from '../../data'

import './Slider.scss'

class Slider extends Component {
    state = {
        products: [],
        currentSlide: 0,
        classAnimationEl: ''
    }

    slideAutoPlayTimer = new Timer(() => this.switchSlide(this.changeCurrentValue('INCREMENT')), this.props.autoPlayInterval)

    componentDidMount() {
        this.setState({products: datadefault.map(item => item)})
        this.slideAutoPlayTimer.start()
    }

    get getSlideImgUrl() {
        return `/images/product/${this.state.products[this.state.currentSlide].id}/${this.state.products[this.state.currentSlide].options.colors[0]}/slide.webp`
    }

    switchSlide = (currentSlide) => {
        this.slideAutoPlayTimer.stop()
        this.setState({classAnimationEl: 'hide'});
        // setTimeout(() => {
        //     this.setState({currentSlide: currentSlide});
        // }, 500)
        setTimeout(() => {
            this.setState({currentSlide: currentSlide, classAnimationEl: 'show'});
            this.slideAutoPlayTimer.start()
        }, 1000)
    }

    changeCurrentValue = action => {
        let currentSlide = this.state.currentSlide
        const productsLength = this.state.products.length

        if (action === 'INCREMENT') currentSlide++
        if (action === 'DECREMENT') currentSlide--

        currentSlide = (currentSlide >= productsLength) ? 0 : (currentSlide <= -1) ? productsLength - 1 : currentSlide
        return currentSlide
    }

    prevSlide = () => this.switchSlide(this.changeCurrentValue('DECREMENT'))
    nextSlide = () => this.switchSlide(this.changeCurrentValue('INCREMENT'))
    getSelectedSlide = event => {
        if (event.target.tagName !== 'LI') return
        this.switchSlide(+event.target.id)
    }

    render() {
        const classAnimationEl = this.state.classAnimationEl

        return (
            <>
                <div className="slider">
                    <div className="slider__conteiner">
                        <div className="slider__row">
                            <div className="slider__desc">New colections<p>Spring</p></div>
                            <button className="slider__btn-more">More info</button>
                        </div>
                        <div className="slider__row">
                            <div
                                onMouseOver={() => this.slideAutoPlayTimer.stop()}
                                onMouseOut={() => this.slideAutoPlayTimer.start()}
                                className={`slider__img-box ${classAnimationEl}`}>
                                {this.state.products.length === 0 ? null : (
                                    <img
                                        src={this.getSlideImgUrl}
                                        alt="01"
                                        className="slider__img"
                                    />
                                )}
                            </div>
                        </div>
                        <div className="slider__row">
                            <div className="slider__label">new</div>
                            <ul
                                className="slider__nav-dots"
                                onClick={this.getSelectedSlide}
                            >
                                {this.state.products.map((item, index) => (
                                        <li
                                            id={index}
                                            key={index}
                                            className={`slider__nav-dot ${index === this.state.currentSlide ? 'active' : null}`}
                                        />
                                    )
                                )}

                            </ul>
                        </div>
                    </div>

                    <div
                        className="slider__nav">
                        <div className="slider__title">SNKPS OF <br/> THE WEEK</div>
                        <button
                            className="slider__nav-btn slider__nav-btn--prev"
                            onClick={this.prevSlide}

                        >&lt;</button>
                        <button
                            className="slider__nav-btn slider__nav-btn--next"
                            onClick={this.nextSlide}
                        >
                            &gt;
                        </button>
                    </div>
                </div>
            </>
        );
    }
}

export default Slider;