import React, {Component} from 'react';
import produkts from "../../data";
import './ProductPage.scss'
import ProductSlider from "../ProductSlider/ProductSlider"
import ProductDescription from '../ProductDescription/ProductDescription'

class ProductPage extends Component {

    state = {
        currentVariant: 0,
    }

    get getCurrentVariant() {
        const currentVariant = this.state.currentVariant
        return this.props.product.options.colors[currentVariant]
    }

    render() {
        const product = this.props.product
        return (
            <div className="product-page__row">
                <div className="product-page__column">
                    <ProductDescription product={product}/>
                </div>
                <div className="product-page__column">
                    <ProductSlider
                        product={{...product, variant: this.getCurrentVariant}}
                    />
                </div>
            </div>
        )
            ;
    }
}

export default ProductPage;